<template>
  <component
    :is="tagName"
    :class="classes"
    :disabled="disabled"
    @click="handleClickLink"
    :target="target"
  >
    <Icon v-if="icon" :type="icon" />
    <span><slot>{{label}}</slot></span>
  </component>
</template>
<script>

/**
* @param {string} type Button 
* @param {string|number} size 
* @param {boolean} isLoading 
* @param {string} to 
* @param {boolean} ghost 
* @param {string} target  '_self'
* @param {bolean} disabled 
* @param {bolean} select 
* @param {bolean} square 
*/
import { oneOf, findComponentUpward } from '../../mixins/utils/assist'
import mixinsLink from '../../mixins/utils/link'

const prefixCls = 'kui-btn'

export default {
  name: 'Button',
  mixins: [ mixinsLink ],
  // components: { Icon },
  props: {
    type: {
      validator (value) {
        return oneOf(value, [
          'default',
          'primary',
          'dashed',
          'text',
          'info',
          'success',
          'warning',
          'error'
        ])
      },
      default: 'default'
    },
    target: {
      validator (value) {
        return oneOf(value, [
          '_blank',
          '_parent',
          '_self',
          '_top'
        ])
      },
      default: '_self'
    },
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      },
      default: 'default'
    },
    loading: Boolean,
    disabled: Boolean,
    icon: {
      type: String,
      default: ''
    },
    ghost: {
      type: Boolean,
      default: false
    },
    select: {
      type: Boolean,
      default: false
    },
    square: {
      type: Boolean,
      default: false
    },
    label: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      model: this.select
    }
  },
  computed: {
    classes () {
      return [
        `${prefixCls}`,
        `${prefixCls}-${this.type}`,
        {
          [`${prefixCls}-${this.shape}`]: !!this.shape,
          [`${prefixCls}-${this.size}`]: this.size !== 'default',
          [`${prefixCls}-loading`]: this.loading != null && this.loading,
          [`${prefixCls}-ghost`]: this.ghost,
          [`${prefixCls}-current`]: this.model,
          [`${prefixCls}-square`]: this.square
        }
      ]
    },
    tagName () {
      return this.to ? 'a' : 'button'
    }
    // model () {
    //   return this.select
    // }
  },
  methods: {
    handleClickLink (event) {
      if (this.disabled) {
        return
      }
      let router = this.$router
      let current = this.$route
      if (this.to) {
        if (this.target === '_blank') {
          window.open(this.to)
        } else {
          const to = router.resolve(this.to, current).href
          window.location.href = to
        }
      }
      this.$emit('click', event)
      const parent = findComponentUpward(this, 'CheckboxGroup')
      if (parent) {
        parent.change(this.label)
      }
    }
  }
}
</script>
<style lang="less" scoped src="./index.less"></style>
