<template>
  <transition name="fade">
    <div v-if="show" class="kui-toast" :style="styles">
      <div class="kui-toast-body">
        <div class="kui-toast-body-icon">
          <Icon :type="type" size="40"/>
        </div>
        <div class="kui-toast-body-text" v-html="content"></div>
      </div>
    </div>
  </transition>
</template>

<script>
/**
* @param {string} type   success | warning
* @param {number} duration 
* @param {number} content 
* @param {number} width 
*/
const prefixCls = 'kui-toast'
export default {
  props: {
  },
  data () {
    return {
      show: false,
      type: '',
      width: 272,
      duration: 2,
      content: ''
    }
  },
  computed: {
    styles () {
      const style = {}
      if (this.width) {
        style['width'] = `${this.width}px`
      }
      return style
    },
    classes () {
      return [
        `${prefixCls}-content`,
        {
          [`${prefixCls}-content-show`]: this.show,
          [`${prefixCls}-content-hidden`]: !this.show
        }
      ]
    }
  },
  created () {
    setTimeout(() => {
      this.close()
    }, this.duration * 1000)
  },
  methods: {
    close () {
      this.show = false
    }
  }
}
</script>
<style lang="less" scoped src="./index.less"></style>
